.App {
  text-align: center;
}

.App-header {
  background-color: #222;
  height: 10%;
  padding: 20px;
  color: white;
}

.repoList {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.results {
  text-align: center;
}
.userBadge {
  height: 20%; 
  padding: 1% 1% 0% 1%;
  margin: 0 auto;
  width: 40%;
}

@media screen and (min-width: 1201px) {
  .userBadge {
    font-size: 100%;
  }
  .searchComponent > form > input {
    width: 20%;
    font-size: 100%;
    text-align: center;
    margin: 0.5%;
    border-style: solid;
    border-color: rgba(0, 0, 0, 0.182);
    border-width: 0.5px;
  }
  .repoInfo {
    width: 31%;
    max-height: 5%;
    min-height: 5%;
    margin: 1%;
    background-color: #f7f8f9;
    border-style: solid;
    border-color: black;
    border-width: 0.5px;
    word-wrap: break-word;
  }
  .icons {
    padding: 1%;
  }
  button:focus, input:focus {
    outline: none;
  }
  .error {
    color: red;
  }
  .searchComponent > form > button {
    background: #3498db;
    background-image: -webkit-linear-gradient(top, #3498db, #2980b9);
    background-image: -o-linear-gradient(top, #3498db, #2980b9);
    background-image: -webkit-gradient(linear, left top, left bottom, from(#3498db), to(#2980b9));
    background-image: linear-gradient(to bottom, #3498db, #2980b9);
    border-radius: 30px;
    font-family: Arial;
    color: #ffffff;
    font-size: 100%;
    padding: 10px 20px 10px 20px;
    text-decoration: none;
  }
  
  .searchComponent > form > button:hover {
    background: #3cb0fd;
    background-image: -webkit-linear-gradient(top, #3cb0fd, #3498db);
    background-image: -o-linear-gradient(top, #3cb0fd, #3498db);
    background-image: -webkit-gradient(linear, left top, left bottom, from(#3cb0fd), to(#3498db));
    background-image: linear-gradient(to bottom, #3cb0fd, #3498db);
    text-decoration: none;
  }
  
  .btn:hover {
    background: #3cb0fd;
    background-image: -webkit-linear-gradient(top, #3cb0fd, #3498db);
    background-image: -o-linear-gradient(top, #3cb0fd, #3498db);
    background-image: -webkit-gradient(linear, left top, left bottom, from(#3cb0fd), to(#3498db));
    background-image: linear-gradient(to bottom, #3cb0fd, #3498db);
    text-decoration: none;
  }
  .badgeAvatar {
    max-height: 25%;
    max-width: 25%;
    border-radius: 50%;
  }
}

